body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 255, 0.233); }

.panel-login {
  position: absolute;
  width: 900px;
  max-width: 100%;
  height: 450px;
  top: -10%;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  background: #3c415e;
  color: white;
  border-radius: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row nowrap;
  flex-flow: row nowrap;
  overflow: hidden; }

.botonSinEstilo {
  border-style: none;
  outline-style: none;
  background: none;
  cursor: pointer; }

.panel-login-1 {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column wrap;
  flex-flow: column wrap;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-align-items: center;
  align-items: center;
  background: white;
  width: 50%; }

.logoM {
  width: 50%;
  height: auto;
  margin: 5%; }

.contenedor-input {
  width: 65%;
  height: 9%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
  justify-content: flex-start;
  border: 1px solid #9c9c9c;
  box-shadow: 0px 1px 15px #b8ebff, 0px -1px 15px #b8ebff;
  margin: 2%;
  border-radius: 10px; }

.contenedor-boton {
  width: 65%;
  height: 9%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row nowrap;
  flex-flow: row nowrap;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  margin: 2%; }

.icono {
  width: 30px;
  height: 30px;
  margin-top: 1%;
  margin-left: 5px; }

#pass, #mail {
  width: 80%;
  height: 35px;
  margin-left: 3%;
  border: none;
  outline-style: none; }

.boton-enviar {
  border-radius: 15px;
  width: 50%;
  height: 35px;
  padding: 3%;
  background: -webkit-linear-gradient(#7edbe7, #1cb3c8);
  background: linear-gradient(#7edbe7, #1cb3c8);
  color: white;
  outline-style: none;
  font-weight: bold;
  cursor: pointer; }

.boton-nuevaCuenta {
  color: #2061ac;
  width: 35%; }

.panel-login-2-3 {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column wrap;
  flex-flow: column wrap;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-align-items: center;
  align-items: center;
  text-align: center;
  font-size: 1em;
  margin: 2.5%;
  height: 90%;
  width: 45%; }

.descripcion-text {
  color: #b1b1b1;
  font-size: 0.9em;
  margin-top: -2%; }

.fallo {
  font-size: 0.7em;
  color: red; }

.panel-login-4 input {
  font-size: 0.9em;
  background: none;
  border-style: none;
  outline-style: none;
  padding-left: 3%; }

.panel-login-4 {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column wrap;
  flex-flow: column wrap;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-align-items: center;
  align-items: center;
  background: white;
  width: 50%; }

@media (max-width: 720px) {
  .panel-login {
    border-radius: 0px;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column nowrap;
    flex-flow: column nowrap; }
  .panel-login-1, .panel-login-4 {
    width: 100%;
    height: 70%; }
  .panel-login-2-3 {
    width: 100%;
    height: 30%; } }

